<template>
    <table cellspacing="0" cellpadding="0">
        <tbody>
            <tr>
                <th v-if="(type=='all'||type=='agent')&&userType!=3"></th>
                <th v-if="type=='all'&&userType!=3"></th>
                <th></th>
                <th v-if="userType!=3"></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="(item,index) in list" :class="{tr2:index%2}">
                <td v-if="(type=='all'||type=='agent')&&userType!=3">
                    <router-link :to="{query:{customer_id:item.client_id,customer_name:item.company}}">{{item.company}}</router-link>
                </td>
                <td v-if="type=='all'&&userType!=3">
                    <router-link :to="{query:{agent_id:item.superior_id,agent_name:item.name}}">{{item.name}}</router-link>
                </td>
                <td>{{item.seat_num}}</td>
                <td v-if="userType!=3">{{item.price}}/</td>
                <td>{{item.cost}}</td>
                <td>{{item.ask_time}}</td>
                <td>{{item.stop_time}}</td>
            </tr>
        </tbody>
    </table>
</template>
<script>
    import API from 'src/services/api'

    export default {
        props: ['list', 'type'],
        data() {
            let user = JSON.parse(localStorage.getItem('user'))
            return {
                userType: user.type,
            }
        }
    }

</script>


